.derecho {
  background-color: #2b2f40;
  color: whitesmoke;
  width: 350%;
  height: 180%;
}

.next {
  display: flex;
  justify-content: space-around;
  padding-right: 30%;
  padding-top: 3%;
}

.dia {
  background-color: #40455b;
}

.maxmin {
  display: flex;
  justify-content: center;
  gap: 25%;
}

.high {
  padding-top: 5%;
  padding-left: 2%;
}
.destacados {
  text-align: center;
  padding-left: 2%;
}

.des {
  background-color: #40455b;
  border: 2px solid #2b2f40 ;
}

.hnum {
  font-weight: bold;
}
.botones {
  display: flex;
  padding-left: 90%;
  padding-top: 1%;
  gap: 10px;
}

@media (max-width: 576px ){

    .destacados{
        text-align: center;
        padding-left: 0%;
        width: 104%;
    }

    .derecho{
        width: 100%;
    }
    .next{
        width: 106%;
        padding-right: 0%;
        padding-top: 0%;
      
    }

    .dia {
        text-align: center;
      }

      .high {
        padding-top: 5%;
        padding-left: 28%;
        padding-bottom: 5%;
    }
}

@media (max-width: 768px ){

    .derecho{
        width: 100%;
        height: 80%;
    }
  
}